ing: 10px;\n    }\n\n}\n",".tutorial-holder1 {\n    display: flex;\n    justify-content: center;\n    grid-column: 1/8;\n    grid-row: 1;\n    z-index: 2;\n    width: 100%;\n    height: 100%;\n    \n    #box-wrap {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        flex-direction: column;\n        padding-top: 10px;\n        margin: auto auto;\n        margin-top: 100px;\n        height: 200px;\n        width: 600px;\n        background: #fff;\n\n        #tutorial-text {\n            padding: 10px;\n            padding-left: 30px;\n            padding-right: 30px;\n        }\n\n        p {\n            margin-left: 30px;\n            margin-right: 30px;\n        }\n\n        #tut-button1 {\n            height: 40px;\n            width: 80px;\n            justify-content: center;\n            margin-bottom: 10px;\n\n        }\n    }\n\n}\n\n.tutorial-holder2 {\n    display: flex;\n    justify-content: center;\n    grid-column: 1/8;\n    grid-row: 1;\n    z-index: 2;\n    width: 100%;\n    height: 100%;\n    \n    #box-wrap {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        flex-direction: column;\n        padding-top: 10px;\n        margin-right: 40%;\n        margin-top: 100px;\n        height: 200px;\n        width: 600px;\n        background: #fff;\n\n        #tutorial-text {\n            padding: 10px;\n            padding-left: 30px;\n            padding-right: 30px;\n        }\n\n        p {\n            margin-left: 30px;\n            margin-right: 30px;\n        }\n\n        #tut-button2 {\n            height: 40px;\n            width: 80px;\n            justify-content: center;\n            margin-bottom: 10px;\n\n        }\n    }\n\n}\n\n.tutorial-holder3 {\n    display: flex;\n    justify-content: center;\n    grid-column: 1/8;\n    grid-row: 1;\n    z-index: 2;\n    width: 100%;\n    height: 100%;\n    \n    #box-wrap {\n        display: flex;\n    